import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";

export const addRoomPackages = async (data, isPost) => {
  const request = await axios({
    method: isPost ? "post" : "put",
    url: endpoints.roomPackages.ADD_ROOM_PACKAGES,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: data,
  }).catch(axiosHelpers);
  return request;
};
export const updateRoomPackageDetails = async (body) => {
  const request = await axios({
    method: "put",
    url: endpoints.roomPackages.ADD_ROOM_PACKAGES,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: body,
  }).catch(axiosHelpers);
  return request;
};
export const getRoomPackageDetails = async (propertyId, roomTypeId, params) => {
  const request = await axios({
    method: "get",
    url: endpoints.roomPackages.GET_ROOM_PACKAGES + `/${propertyId}`,
    params,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
  }).catch(axiosHelpers);
  return request;
};

export const getRoomTypesList = async (propertyId, params) => {
  const request = await axios({
    method: "get",
    url: endpoints.roomPackages.GET_ROOM_TYPE_LIST + `/${propertyId}`,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    params,
  }).catch(axiosHelpers);
  return request;
};

